

.sticky{
	background-color: white;
	display: flex;
	position: fixed;
	top: 0;
	width: 100%;
	height: 10vh;
	z-index: 10;
}


.res{

	width: 5%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items:center; 
	margin-left: auto;
	
	
}
.res-icon{
	color: #00cdac;
}
.Nav-Logo{
	width: 5%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items:center; 
	padding: 10px;
	
}
.Logo-Style{
	height: 50px;
	width: 50px;
}

@media only screen and (max-width: 600px){
    .res{
        width: 10%;

    }

.Nav-Logo{
	width:20%;

}
.Logo-Style{
	height: 40px;
	width: 40px;
}

}
@media only screen and (max-width: 800px) and (min-width:600px) {
    
.res{
        width: 10%;


    }
.Nav-Logo{
	width:10%;

}
}

@media only screen and (max-width: 1024px) and (min-width:800px) {
    
.res{
        width: 10%;
        

    }
 .res-icon{
	width:  80px;
	height: 50px;
}
.Nav-Logo{
	width:10%;

}
}